import React, { useState, useEffect, useCallback } from "react";
import { Modal, Button, Spinner } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  sendEmailVerification,
} from "firebase/auth";
import { auth } from "./Firebase";
//import { signOut } from 'firebase/auth';

/*
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
*/
import { Outlet } from "react-router-dom";
import { scroller } from "react-scroll";
import { sendPasswordResetEmail } from "firebase/auth";

//import { useNavigate } from 'react-router-dom';

export default function Navbar() {
  const [loading, setLoading] = useState(false); // State variable to track loading state

  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");

  const [loggedInUser, setLoggedInUser] = useState();

  const [showProfileModal, setShowProfileModal] = useState(false);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleProfileModalClose = useCallback(() => setShowProfileModal(false));
  const handleProfileModalShow = () => setShowProfileModal(true);
  //const navigate = useNavigate();

  //forgot passgot password logic
  const handleForgotPassword = async () => {
    try {
      await sendPasswordResetEmail(auth, loginEmail);
      alert("Password reset email sent. Please check your email inbox.");
    } catch (error) {
      console.error("Error sending password reset email:", error.message);
      alert("An error occurred. Please try again later.");
    }
  };

  const handleLogout = useCallback(() => {
    // Clear authentication state and login timestamp from local storage
    localStorage.removeItem("loggedInUser");
    localStorage.removeItem("loginTime");
  
    setLoggedInUser(null);
    handleProfileModalClose();
  }, [setLoggedInUser, handleProfileModalClose]);
  
  // Check if the user's session is still valid upon page load
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("loggedInUser"));
    const loginTime = parseInt(localStorage.getItem("loginTime"), 10);
    if (user && loginTime) {
      const now = new Date().getTime();
      const sessionDuration = now - loginTime;
      if (sessionDuration <= 600000) { // 10 minutes in milliseconds
        setLoggedInUser(user);
      } else {
        // Session expired, log the user out
        handleLogout();
      }
    }
  }, [setLoggedInUser, handleLogout]);

  const scrollToSection = (sectionId) => {
    scroller.scrollTo(sectionId, {
      duration: 100,
      delay: 0,
      smooth: "easeInOutQuart",
    });
  };

  const [showDashboard, setShowDashboard] = useState(false);

  const handleDashboardClick = () => {
    setShowDashboard(!showDashboard);
  };

  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showSignupModal, setShowSignupModal] = useState(false);

  const handleLoginClose = () => setShowLoginModal(false);
  const handleLoginShow = () => setShowLoginModal(true);

  const handleSignupClose = () => setShowSignupModal(false);
  const handleSignupShow = () => setShowSignupModal(true);

  const handleSignupafterLogin = () => {
    handleLoginClose(false); // Close the login modal
    handleSignupShow(true); // Show the signup modal
  };

  const handleLoginafterSignup = () => {
    handleSignupClose(false); // close the signup modal
    handleLoginShow(true); // show the login modal
  };

  const [signupFirstName, setSignupFirstName] = useState("");
  const [signupLastName, setSignupLastName] = useState("");
  const [signupEmail, setSignupEmail] = useState("");
  const [signupPassword, setSignupPassword] = useState("");

  const isEmailValid = (email) => {
    // Regular expression for validating email format
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const isPasswordValid = (password) => {
    // Regular expression for validating password format
    // At least 6 characters, including at least one uppercase letter, one lowercase letter, and one number
    const passwordRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,}$/;
    return passwordRegex.test(password);
  };

  const handleLogin = async (e) => {
    e.preventDefault();
  
    try {
      setLoading(true);
      const userCredential = await signInWithEmailAndPassword(
        auth,
        loginEmail,
        loginPassword
      );
      const user = userCredential.user;
      if (user && user.emailVerified) {
        setLoading(false);
        alert("Login successful!");
        setLoggedInUser({ displayName: user.displayName, email: user.email });
  
        // Store authentication state and login timestamp in local storage
        const now = new Date().getTime();
        localStorage.setItem("loggedInUser", JSON.stringify(user));
        localStorage.setItem("loginTime", now);
  
        handleLoginClose();
      } else if (user && !user.emailVerified) {
        alert("Email not verified. Please verify your email.");
      }
    } catch (error) {
      if (error.code === "auth/user-not-found" || error.code === "auth/wrong-password") {
        alert("Incorrect email or password. Please try again.");
      } else {
        alert("An error occurred. Please try again later.");
      }
    } finally {
      setLoading(false); // Set loading back to false when login process completes
    }
  };
  

  const handleSignup = async (e) => {
    e.preventDefault();
    setLoading(true);

    const email = signupEmail.trim();
    const password = signupPassword.trim();

    // Perform email and password validation
    if (!isEmailValid(email)) {
      setLoading(false); // Reset loading state
      alert("Please enter a valid email address.");
      return;
    }

    if (!isPasswordValid(password)) {
      setLoading(false); // Reset loading state
      alert(
        "Password must be at least 6 characters long and contain at least one uppercase letter, one lowercase letter, and one number."
      );
      return;
    }

    try {
      await createUserWithEmailAndPassword(auth, signupEmail, signupPassword);
      const user = auth.currentUser;
      await sendEmailVerification(user);
      setLoading(false);
      alert(
        "Sign up successful and Verification email sent to " +
          user.email +
          "  Please Verify the Email before you Log in "
      );
      handleLoginafterSignup(); // Close the signup modal
      // Handle successful signup (e.g., redirect, update UI)
    }catch (error) {
      if (error.code === "auth/email-already-in-use") {
        alert("An account with this email already exists. Please log in.");
      } else {
        setLoading(false); // Reset loading state
        console.error("Signup error:", error.message);
        // Handle other signup errors
        alert("An error occurred during signup. Please try again later.");
        
      }
      handleLoginafterSignup();
    }
  };

  return (
    <>
      {/* Loader */}
      {loading && (
        <div className="loader-container">
          <Spinner animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
          </Spinner>
        </div>
      )}
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
        <div className="container-fluid">
          <a className="navbar-brand" href="/" style={{ color: "#2cd1e8" }}>
            <b>
              <img src="/Logo2.png" alt="Your Logo" height="30" />
            </b>
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li>
                <text
                  style={{ cursor: "pointer", marginRight: "15px" }}
                  className="text-white"
                  to="/"
                  onClick={() => scrollToSection("mainwindow")}
                >
                  {" "}
                  Home{" "}
                </text>
              </li>
              <li>
                <text
                  className="text-white"
                  style={{ cursor: "pointer", marginRight: "15px" }}
                  to="/aboutus"
                  onClick={() => scrollToSection("aboutus")}
                >
                  {" "}
                  About Us{" "}
                </text>
              </li>
              <li>
                <text
                  className="text-white"
                  style={{ cursor: "pointer", marginRight: "15px" }}
                  to="/faq"
                  onClick={() => scrollToSection("faq")}
                >
                  {" "}
                  FAQ{" "}
                </text>
              </li>
            </ul>

            {/* Conditionally render based on whether the user is logged in */}

            {loggedInUser ? (
              <div className="d-flex justify-content-between">
                <span
                  role="img"
                  aria-label="upload"
                  onClick={handleDashboardClick}
                  style={{ cursor: "pointer" }}
                >
                  <div className="text-white" onClick={handleProfileModalShow}>
                    Welcome {loggedInUser.email}
                  </div>
                </span>
              </div>
            ) : (
              <>
                <div>
                  <text
                    className="nav-link active m-2 text-white bg-#54777C"
                    aria-current="page"
                    style={{ cursor: "pointer" }}
                    onClick={handleLoginShow}
                  >
                    LOG IN
                  </text>
                </div>
                <div>
                  <button
                    className="btn btn-outline-success bg-#54777C text-white"
                    type="button"
                    onClick={handleSignupShow}
                  >
                    SIGN UP
                  </button>
                </div>
              </>
            )}
          </div>
        </div>
      </nav>
      <Outlet />

      {/* Login Modal */}

      <div>
        <Modal show={showLoginModal} onHide={handleLoginClose} centered>
          <Modal.Header closeButton>
            <Modal.Title>LOGIN</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {/* Add your login form elements here */}
            <form onSubmit={handleLogin}>
              <div className="form-group">
                <label>Email address</label>
                <input
                  type="email"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                  required
                  value={loginEmail}
                  onChange={(e) => setLoginEmail(e.target.value)}
                />
                <small id="emailHelp" className="form-text text-muted">
                  We'll never share your email with anyone else.
                </small>
              </div>
              <div className="form-group">
                <label for="exampleInputPassword1">Password</label>
                <input
                  type="password"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Password"
                  required
                  value={loginPassword}
                  onChange={(e) => setLoginPassword(e.target.value)}
                />
              </div>
              <div className="d-grid">
                <button
                  id="login"
                  type="submit"
                  className="btn btn-primary mt-3"
                  onSubmit={handleLogin}
                >
                  Log in
                </button>
              </div>
              {/* Loader */}
              {loading && (
                <div className="d-flex justify-content-center align-items-center loader-container">
                  <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </Spinner>
                </div>
              )}
            </form>
            <div className="text-center mt-3">
              <button className="btn btn-link" onClick={handleForgotPassword}>
                Forgot Password?
              </button>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <p>
              New User?
              <Button
                variant="primary"
                onClick={handleSignupafterLogin}
                style={{ marginLeft: "8px" }}
              >
                Sign up
              </Button>
            </p>
          </Modal.Footer>
        </Modal>

        {/* Signup Modal */}
        <Modal show={showSignupModal} onHide={handleSignupClose} centered>
          <Modal.Header closeButton>
            <Modal.Title>SIGN UP</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {/* Add your login form elements here */}

            <div className="mb-3">
              <label>First name</label>
              <input
                type="text"
                className="form-control"
                placeholder="First name"
                required
                value={signupFirstName}
                onChange={(e) => setSignupFirstName(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <label>Last name</label>
              <input
                type="text"
                className="form-control"
                placeholder="Last name"
                required
                value={signupLastName}
                onChange={(e) => setSignupLastName(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <label>Email address</label>
              <input
                type="email"
                className="form-control"
                placeholder="Enter email"
                required
                value={signupEmail}
                onChange={(e) => setSignupEmail(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <label>Password</label>
              <input
                type="password"
                className="form-control"
                placeholder="Enter password"
                required
                value={signupPassword}
                onChange={(e) => setSignupPassword(e.target.value)}
              />
            </div>

            <div className="d-grid">
              <button
                id="signin"
                type="submit"
                className="btn btn-primary"
                onClick={handleSignup}
              >
                Sign Up
              </button>
            </div>

            {/* Loader */}
            {loading && (
              <div className="d-flex justify-content-center align-items-center loader-container">
                <Spinner animation="border" role="status">
                  <span className="visually-hidden">Loading...</span>
                </Spinner>
              </div>
            )}

            <p className="forgot-password text-right mt-3">
              Already registered ?{" "}
              <text
                style={{ cursor: "pointer" }}
                onClick={handleLoginafterSignup}
              >
                <b>Log in</b>{" "}
              </text>
            </p>
          </Modal.Body>
        </Modal>

        {/*profile modal */}
        <Modal
          show={showProfileModal}
          onHide={handleProfileModalClose}
          dialogClassName="profile-modal"
        >
          <Modal.Header closeButton>
            <Modal.Title>User Profile</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>
              <strong>Email:</strong>{" "}
              {loggedInUser ? loggedInUser.email : "Not logged in"}
            </p>
            {/* Add more profile information here */}
          </Modal.Body>
          <Modal.Footer>
            <button className="btn btn-danger" onClick={handleLogout}>
              Logout
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    </>
  );
}
