import React from 'react';

const Faq = () => {
  return (
    <div id='faq' className="container mt-5">
      <h1 className="mb-4 text-white">FAQ</h1>
      <div className="accordion" id="faqAccordion">
        {/* FAQ item 1 */}
        <div className="accordion-item">
          <h2 className="accordion-header" id="headingOne">
            <button
              className="accordion-button"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseOne"
              aria-expanded="true"
              aria-controls="collapseOne"
            >
               How does the Speaker Identification System work?
            </button>
          </h2>
          <div
            id="collapseOne"
            className="accordion-collapse collapse show"
            aria-labelledby="headingOne"
            data-bs-parent="#faqAccordion"
          >
            <div className="accordion-body">
            Answer: The Speaker Identification System works by capturing and analyzing unique vocal characteristics of individuals, such as pitch, tone, and pronunciation. These characteristics are then compared with stored voice profiles to verify the identity of the speaker.
            </div>
          </div>
        </div>
        {/* FAQ item 2 */}
        <div className="accordion-item">
          <h2 className="accordion-header" id="headingTwo">
            <button
              className="accordion-button collapsed"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseTwo"
              aria-expanded="false"
              aria-controls="collapseTwo"
            >
               Is the Speaker Identification System accurate?
            </button>
          </h2>
          <div
            id="collapseTwo"
            className="accordion-collapse collapse"
            aria-labelledby="headingTwo"
            data-bs-parent="#faqAccordion"
          >
            <div className="accordion-body">
            Answer: Yes, the Speaker Identification System boasts high accuracy due to its advanced voice recognition technology. It undergoes rigorous testing to ensure reliable identification even in varied environmental conditions.
            </div>
          </div>
        </div>
        {/* Add more FAQ items as needed */}
         {/* FAQ item 3 */}
         <div className="accordion-item">
          <h2 className="accordion-header" id="headingThree">
            <button
              className="accordion-button collapsed"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseThree"
              aria-expanded="false"
              aria-controls="collapseThree"
            >
              Can the system be fooled by recordings or impersonations?
            </button>
          </h2>
          <div
            id="collapseThree"
            className="accordion-collapse collapse"
            aria-labelledby="headingThree"
            data-bs-parent="#faqAccordion"
          >
            <div className="accordion-body">
            Answer: The system incorporates anti-spoofing measures to prevent unauthorized access using recordings or impersonations. It distinguishes between live and pre-recorded voices, enhancing security and reliability.
            </div>
          </div>
        </div>

         {/* FAQ item 4 */}
         <div className="accordion-item">
          <h2 className="accordion-header" id="headingfour">
            <button
              className="accordion-button collapsed"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapsefour"
              aria-expanded="false"
              aria-controls="collapsefour"
            >
              How is the privacy of users' voice data ensured?
            </button>
          </h2>
          <div
            id="collapsefour"
            className="accordion-collapse collapse"
            aria-labelledby="headingfour"
            data-bs-parent="#faqAccordion"
          >
            <div className="accordion-body">
            Answer: User privacy is paramount. The Speaker Identification System employs stringent security measures to safeguard voice data, including encryption during storage and transmission. Access to voice profiles is strictly controlled and governed by privacy policies.
            </div>
          </div>
        </div>

         {/* FAQ item 5 */}
         <div className="accordion-item">
          <h2 className="accordion-header" id="headingfive">
            <button
              className="accordion-button collapsed"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapsefive"
              aria-expanded="false"
              aria-controls="collapsefive"
            >
             What are the applications of the Speaker Identification System?
            </button>
          </h2>
          <div
            id="collapsefive"
            className="accordion-collapse collapse"
            aria-labelledby="headingfive"
            data-bs-parent="#faqAccordion"
          >
            <div className="accordion-body">
            Answer: The system finds applications across various sectors, including access control, online banking, customer service authentication, and law enforcement. Its versatility makes it adaptable to diverse authentication needs.
            </div>
          </div>
        </div>

         {/* FAQ item 6 */}
         <div className="accordion-item">
          <h2 className="accordion-header" id="headingTwo">
            <button
              className="accordion-button collapsed"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapsesix"
              aria-expanded="false"
              aria-controls="collapsesix"
            >
            Can the system handle different languages and accents?
            </button>
          </h2>
          <div
            id="collapsesix"
            className="accordion-collapse collapse"
            aria-labelledby="headingsix"
            data-bs-parent="#faqAccordion"
          >
            <div className="accordion-body">
            Answer: Yes, the Speaker Identification System is designed to accommodate a wide range of languages, accents, and dialects. Its robust algorithms are trained on diverse datasets to ensure accuracy and effectiveness across linguistic variations.
            </div>
          </div>
        </div>
        
        

      </div>
      <br></br><br></br><br></br>
    </div>
  );
};

export default Faq;
