import React, { useState } from "react";
import Card from "react-bootstrap/Card";
//import { getAuth } from "firebase/auth";
//import Navbar from './Navbar';
import { Modal, Button } from "react-bootstrap";
//import { getAuth, onAuthStateChanged } from "firebase/auth";

function TextExample() {
  const [isRecording, setIsRecording] = useState(false);
  const [stream, setStream] = useState(null);

  const [showTrainModal, setTrainModal] = useState(false);
  const [showTestModal, setTestModal] = useState(false);

  const handleTrainModalShow = () => setTrainModal(true);
  const handleTrainModalclose = () => setTrainModal(false);

  const handleTestModalShow = () => setTestModal(true);
  const handleTestModalclose = () => setTestModal(false);

  //logic to set startrecording button on only after succesfull log in
  /*
 const auth = getAuth();

const handleCheckLogIn = async () => {
  const user = auth.currentUser;
  if (user) {
    console.log("User is logged in. Starting recording...");
    handleTrainModalShow();
    handleStartRecording();
  } else {
    alert("Please log in to start recording.");
  }
};
*/
  const handleStartRecording = async () => {
    try {
      // Ask for microphone permission
      console.log("Requesting microphone permission...");
      const userMediaStream = await navigator.mediaDevices.getUserMedia({
        audio: true,
      });

      // Permission granted, start recording
      console.log("Microphone permission granted. Starting recording...");
      setStream(userMediaStream);
      setIsRecording(true);
    } catch (error) {
      // Permission denied or other error occurred
      console.error("Error accessing microphone:", error);
    }
  };

  const handleStopRecording = () => {
    // Stop recording and release microphone stream
    if (stream) {
      stream.getTracks().forEach((track) => track.stop());
      setStream(null);
      setIsRecording(false);
    }
  };
  return (
    <div id="home">
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <Card
        className="mx-auto my-3 "
        style={{
          maxWidth: "50rem",
          background: "#222831",
          boxShadow: "1rem 1rem 1rem 1rem  #54777C",
        }}
      >
        <Card.Body
          className="d-flex flex-column justify-content-center align-items-center"
          style={{ marginTop: "55px" }}
        >
          <Card.Title
            className="text-center text-white"
            style={{ textDecoration: "underline" }}
          >
            <strong
              className="fs-4"
              style={{
                color: "white",
                fontFamily: "CoFo Sans, sans-serif",
                fontWeight: "bold",
                letterSpacing: "2px",
                textShadow: "2px 2px 4px rgba(0, 0, 0, 0.5)",
              }}
            >
              Discover Voice Matching
            </strong>
          </Card.Title>
          <Card.Subtitle className="mb-2 text-white text-center">
            Share your voice and find matching patterns
          </Card.Subtitle>
          <Card.Text className="text-light text-white">
            Connect with others through the uniqueness of your voice. Share and
            explore voice patterns to make meaningful connections.
          </Card.Text>
          <div className="container text-center mt-3 text-white">
            <button
              className="btn btn-outline-success text-white m-2"
              type="button"
              onClick={handleTrainModalShow}
            >
              TRAIN MODEL BY RECORDING
              <span
                role="img"
                aria-label="record"
                style={{ color: "white", marginLeft: "5px" }}
              >
                
              </span>
            </button>

            <button
              className="btn btn-outline-success text-white m-2"
              type="button"
              onClick={handleTestModalShow}
            >
              TEST MODEL BY RECORDING
              <span
                role="img"
                aria-label="record"
                style={{ color: "white", marginLeft: "5px" }}
              >
                
              </span>
            </button>
            {/*
          <label htmlFor="fileInput" className="btn btn-outline-success bg-#54777C text-white m-2">
           TRAIN MODEL BY UPLOADING AUDIO
            <span role="img" aria-label="upload" style={{ marginLeft: '5px' }}>
            
            </span>
            <input id="fileInput" type="file" style={{ display: 'none' }} />
          </label>

          <label htmlFor="fileInput" className="btn btn-outline-success bg-#54777C text-white m-2">
           TEST MODEL BY UPLOADING AUDIO
            <span role="img" aria-label="upload" style={{ marginLeft: '5px' }}>
            
            </span>
            <input id="fileInput" type="file" style={{ display: 'none' }} />
          </label>
  */}
          </div>
        </Card.Body>
      </Card>

      {/* model for train data */}
      <Modal
        show={showTrainModal}
        onHide={handleTrainModalclose}
        backdrop="static"
        keyboard={false}
        centered
      >
        <Modal.Header closeButton onClick={handleStopRecording}>
          <Modal.Title>TRAIN MODEL</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <div>
            <input
              type="text"
              className="form-control"
              placeholder="Enter your model name"
              disabled={isRecording} // Disable input while recording
            />
          </div>
          <br></br>

          {!isRecording && (
            <p style={{ marginLeft: "5px" }}>
              To start recording, press the button below
            </p>
          )}

          {isRecording ? (
            <Button
              className="btn btn-outline-danger text-white m-2"
              type="button"
              onClick={handleStopRecording}
            >
              STOP RECORDING
              <span
                role="img"
                aria-label="stop"
                style={{ color: "white", marginLeft: "5px" }}
              >
                
              </span>
            </Button>
          ) : (
            <button
              className="btn btn-outline-success  m-2"
              type="button"
              onClick={handleStartRecording}
            >
              START RECORDING
              <span
                role="img"
                aria-label="record"
                style={{ color: "white", marginLeft: "5px" }}
              >
                
              </span>
            </button>
          )}

          {isRecording && <p style={{ marginLeft: "5px" }}>Microphone is on</p>}
        </Modal.Body>
      </Modal>

      {/* model for test data */}
      <Modal
        show={showTestModal}
        onHide={handleTestModalclose}
        backdrop="static"
        keyboard={false}
        centered
      >
        <Modal.Header closeButton onClick={handleStopRecording}>
          <Modal.Title>TEST MODEL</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <br></br>

          {!isRecording && (
            <p style={{ marginLeft: "5px" }}>
              To Test your model start recording, press the button below
            </p>
          )}

          {isRecording ? (
            <Button
              className="btn btn-outline-danger text-white m-2"
              type="button"
              onClick={handleStopRecording}
            >
              STOP RECORDING
              <span
                role="img"
                aria-label="stop"
                style={{ color: "white", marginLeft: "5px" }}
              >
                
              </span>
            </Button>
          ) : (
            <button
              className="btn btn-outline-success  m-2"
              type="button"
              onClick={handleStartRecording}
            >
              START RECORDING
              <span
                role="img"
                aria-label="record"
                style={{ color: "white", marginLeft: "5px" }}
              >
                
              </span>
            </button>
          )}

          {isRecording && <p style={{ marginLeft: "5px" }}>Microphone is on</p>}
        </Modal.Body>
      </Modal>

      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
    </div>
  );
}

export default TextExample;
